/*
    Code Tab
*/
div.selectors {
  margin: 0 1% 20px;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  background-color: #e0e0e0;
  border-radius: 5px;
  box-shadow: 2px 2px 2px #b0b0b0;
}

.sel {
  margin: 0 0 30px;
  min-height: 1px;
  font-size: 2.5em;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: bold;
  text-align: center;
}

.sel a {
  text-decoration: none;
  color: inherit;
}
